<template>
    <div class="top">
        <van-tabs v-model="active">
            <van-tab v-for="(item, index) of materialList" :key="index" :title="item.materialTypeDesc">
                <div v-for="(e, i) of item.new" :key="i">
                    <div class="pdfList" @click="gotoPDf2(e, 1)">
                        <!-- 两端对齐 -->
                        <van-row type="flex" class="van_row" justify="space-between">
                            <van-col span="15">《
                                {{
                                    e.materialName.length > 10
                                    ? e.materialName.slice(0, 10) + "..."
                                    : e.materialName
                                }}》</van-col>
                            <van-col span="7" :style="{
                                color: e.read == '已阅读' ? '#1989fa' : '',
                            }">
                                <van-icon v-if="e.read == '已阅读'" color="#1989fa" name="checked" />

                                {{ e.read == "已阅读" ? "已阅读" : "未阅读" }}</van-col>
                        </van-row>
                    </div>
                </div>
            </van-tab>
            <div class="button">
                <van-button class="but2" type="info" @click="readAgree">我已逐页阅读并同意（{{
                    this.read + "/" + materialList.length
                }}）</van-button>
            </div>
            <van-popup v-model="popupShow" :style="{ height: '100%', width: '100%' }">
                <pdf :src="pdfsrc" :page="currentPage" @num-pages="pageCount = $event" @loaded="currentPage = 1"></pdf>
                <div class="pdfpage">
                    <span @click="currentPage > 1 && currentPage--">上一页</span>
                    <span>当前第{{ currentPage || 0 }}页/共{{ pageCount || "∞" }}页</span>
                    <span @click="currentPage < pageCount && currentPage++">下一页</span>
                </div>
                <div class="button">
                    <van-button class="but2" type="info" @click="readAgree2">我已逐页阅读并同意{{ this.read2 + "/" + this.cont2
                    }}</van-button>
                </div>
            </van-popup>
        </van-tabs>
    </div>
</template>

<script>
import Vue from 'vue';
import { Toast } from 'vant';
Vue.use(Toast);
import pdf from "vue-pdf";
import { mapState, mapActions } from "vuex";
import util, {
    clearParams,
    getRelationShip,
    getlastDay,
    getDay,
    reconfigArr,
} from "@/components/_utils";
export default {
    components: { pdf },
    data() {
        return {
            // 页码
            currentPage: 0,
            pageCount: 0,
            // pdf路径
            pdfsrc: "",
            // tab
            active: 1,
            // 阅读的是次数
            read: 0,
            read2: 0,
            cont: "",
            cont2: "",

            // 阅读参数
            outParams: 0,
            // 阅读参数
            inParams: 0,
            productData: JSON.parse(sessionStorage.getItem("productData")),
            materialList: [], //产品条款
            // 获取的pdf路径
            materiaRes: "",
            // 弹出框的值
            popupShow: false,

            // 需求给的排序规则
            productSort: [
                {
                    idSort: "1",
                    nameSort: "产品条款",
                },
                {
                    idSort: "2",
                    nameSort: "投保须知",
                },
                {
                    idSort: "3",
                    nameSort: "投保声明书",
                },
                {
                    idSort: "4",
                    nameSort: "免除责任",
                },
                {
                    idSort: "5",
                    nameSort: "特别提示",
                },
                {
                    idSort: "6",
                    nameSort: "投保提示书",
                },
                {
                    idSort: "7",
                    nameSort: "其他文件",
                },
            ],
            display: "block",
            productData: JSON.parse(sessionStorage.getItem("productData")),
            ENCR: null,
            SINGE_PAGE_from: JSON.parse(
                sessionStorage.getItem("SINGE_PAGE_from")
            ) || {
                userName: "",
                idCard: "",
                dateTime: "",
                phone: "",
                SMSCode: "",
                school: "",
                gradeChoose: "",
                classChoose: "",
                email: "",
                area: "",
            },
            MORE_PAGE_from: JSON.parse(sessionStorage.getItem("MORE_PAGE_from")) || {
                userName: "",
                idCard: "",
                dateTime: "",
                userNameInsured: "",
                idCardInsured: "",
                dateTimeInsured: "",

                phone: "",
                SMSCode: "",
                school: "",
                gradeChoose: "",
                classChoose: "",
                email: "",
                area: "",
            },
            info: { choosedRela: "" },
            inParams2: 1,
        };
    },
    created() { },
    mounted() {
        this.exonerationNoticeCardRisk = JSON.parse(
            sessionStorage.getItem("exonerationNoticeCardRisk")
        );
        if (sessionStorage.getItem("route_radio") == 1) {
            this.info.choosedRela = "01";
        } else {
            console.log(this.SINGE_PAGE_from);
            console.log(this.MORE_PAGE_from);
            this.info.choosedRela = "04";
        }
        if (JSON.parse(sessionStorage.getItem("pdfList")).length > 0) {
            this.materialList = JSON.parse(sessionStorage.getItem("pdfList"));

            this.materialList.map((item, index) => {
                if (
                    (item.materialType && item.materialType == "M08") ||
                    item.materialType == "M11"
                ) {
                    this.materialList.splice(index, 1);
                }
            });
            console.log("删除健康告知", this.materialList);
            this.pdfPreview(0);
            // this.materialList = [
            //     {

            //         materialType: "M05",
            //         materialTypeDesc: "投保声明书",

            //         new: [
            //             {
            //                 materialName: "IPAN产品条爱上大飒飒的款11",
            //                 materialPath: "/card/file/M03/2022/08/08/220808092549000765.pdf",
            //                 materialType: "M012",
            //             },
            //             {
            //                 materialName: "IPAN产品条款12",
            //                 materialPath: "/card/file/M03/2022/08/08/220808092549000765.pdf",
            //                 materialType: "M012",
            //             },
            //             {
            //                 materialName: "IPAN产品条款13",
            //                 materialPath: "/card/file/M03/2022/08/08/220808092549000765.pdf",
            //                 materialType: "M012",
            //             },
            //             {
            //                 materialName: "IPAN产品条款24",
            //                 materialPath: "/card/file/M01/2022/08/12/220812092953000905.pdf",
            //                 materialType: "M01",
            //             },
            //         ],
            //     },
            //     {

            //         materialType: "M05",
            //         materialTypeDesc: "产品条款",

            //         new: [
            //             {
            //                 materialName: "IPAN产品条款3",
            //                 materialPath: "/card/file/M03/2022/08/08/220808092549000765.pdf",
            //                 materialType: "M012",
            //             },
            //             {
            //                 materialName: "IPAN产品条款4",
            //                 materialPath: "card/file/M03/2022/08/08/220808092549000765.pdf",
            //                 materialType: "M01",
            //             },
            //         ],
            //     },
            //     {

            //         materialType: "M05",
            //         materialTypeDesc: "投保声明书1",

            //         new: [
            //             {
            //                 materialName: "IPAN产品条款3",
            //                 materialPath: "/card/file/M03/2022/08/08/220808092549000765.pdf",
            //                 materialType: "M012",
            //             },
            //             {
            //                 materialName: "IPAN产品条款4",
            //                 materialPath: "card/file/M03/2022/08/08/220808092549000765.pdf",
            //                 materialType: "M01",
            //             },
            //         ],
            //     },
            //     {

            //         materialType: "M06",
            //         materialTypeDesc: "投保声明书1",

            //         new: [
            //             {
            //                 materialName: "IPAN产品条款3",
            //                 materialPath: "/card/file/M03/2022/08/08/220808092549000765.pdf",
            //                 materialType: "M012",
            //             },
            //             {
            //                 materialName: "IPAN产品条款4",
            //                 materialPath: "card/file/M03/2022/08/08/220808092549000765.pdf",
            //                 materialType: "M01",
            //             },
            //         ],
            //     },
            //     {

            //         materialType: "M07",
            //         materialTypeDesc: "投保声明书1",

            //         new: [
            //             {
            //                 materialName: "IPAN产品条款3",
            //                 materialPath: "/card/file/M03/2022/08/08/220808092549000765.pdf",
            //                 materialType: "M012",
            //             },
            //             {
            //                 materialName: "IPAN产品条款4",
            //                 materialPath: "card/file/M03/2022/08/08/220808092549000765.pdf",
            //                 materialType: "M01",
            //             },
            //         ],
            //     },
            // ];


            // 过滤出其他文件类型
            let qita = this.materialList.filter(item => {
                return item.materialType == 'M06' || item.materialType == 'M07' || item.materialType == 'M08' || item.materialType == 'M09' || item.materialType == 'M10'
            })
            let qitanew = qita.map(e => {
                return e.new
            })
            let qitaList = {
                materialTypeDesc: "其他文件",
            }
            qitaList.new = [].concat(...qitanew)
            console.log('其他文件', qitaList);
            if (qitaList.new.length > 1) {
                this.materialList.push(qitaList)
            }
            // 排序
            let paixun = [];
            this.materialList.forEach((e) => {
                this.productSort.forEach((item) => {
                    e.materialTypeDesc == item.nameSort && paixun.push({ ...e, ...item });
                });
            });
            paixun.sort((a, b) => {
                return a.idSort - b.idSort;
            });
            this.materialList = paixun;
            console.log("最终的materialList", this.materialList);



        } else {
            // this.display = "none";
            // this.checkHold(this.params);
        }
    },
    methods: {
        ...mapActions([
            "checkHold",
            "cardCollectionImport",
            "mobileCheck",
            "orderInfoUpdate",
        ]),
        // 外面点击阅读同意
        readAgree() {
            console.log(this.outParams);
            if (this.read == this.materialList.length) {
                this.checkHold(this.params);
                return;
            }
            let a = this.materialList[this.outParams].new.every((e) => {
                return e.read == "已阅读";
            });
            console.log("a", a);
            if (a) {
                this.read++;
                if (this.read == this.materialList.length) {
                    this.read = this.materialList.length;
                    return;
                }
                this.active = this.read;
                this.outParams++;
                this.c = 0;
                this.read2 = 0;
                this.inParams = 0;
            } else {
                this.popupShow = true;
                this.gotoPDf(this.materialList[this.outParams].new[this.inParams]);
            }
            this.cont2 = this.materialList[this.outParams].new.length;
        },
        // 里面点击阅读同意
        readAgree2() {
            if (this.cont2 > this.inParams) {
                this.materialList[this.outParams].new[this.inParams].read = "已阅读";
                console.log("this.materialList", this.materialList);
                this.read2 = this.materialList[this.outParams].new.filter((e) => {
                    return e.read == "已阅读";
                }).length;
                this.cont2 = this.materialList[this.outParams].new.length;
                this.inParams++;
                console.log("cont", this.cont2, this.inParams);
                if (this.cont2 > this.inParams) {
                    this.gotoPDf(this.materialList[this.outParams].new[this.inParams]);
                }
            } else {
                this.popupShow = false;
            }
        },
        // 打开pdf弹出框
        gotoPDf(item, e) {
            this.popupShow = true;
            const BASE_URL = process.env.VUE_APP_BASE_URL || "";
            const baseUrl =
                location.origin +
                location.pathname.replace("/elife-card", "") +
                BASE_URL;
            const path = item.materialPath;
            this.pdfsrc = pdf.createLoadingTask(baseUrl + "images" + path);
            console.log("pdfsrc", this.pdfsrc);
            console.log("item", item);
        },
        gotoPDf2(item, e) {
            console.log("item", item);
            if (item.read) {
                this.popupShow = true;
                const BASE_URL = process.env.VUE_APP_BASE_URL || "";
                const baseUrl =
                    location.origin +
                    location.pathname.replace("/elife-card", "") +
                    BASE_URL;
                const path = item.materialPath;
                this.pdfsrc = pdf.createLoadingTask(baseUrl + "images" + path);
                console.log("pdfsrc", this.pdfsrc);
            }
            else {
                Toast('按顺序阅读后，方可查看。');
            }
        },
        // 获取每一条数据
        pdfPreview(index) {
            const productMaterialDetail = reconfigArr(this.materialList, index);
            Promise.all(
                productMaterialDetail.map((v) => {
                    if (v.materialType) {
                        return this.$api.common.getProductMaterialDetail({
                            prdId: this.productData.prd_id,
                            ...v,
                        });
                    } else {
                        return v;
                    }
                })
            ).then((res) => {
                console.log(res, "res");
                let a = res.map((e) => {
                    return e.data;
                });
                this.$nextTick(() => {
                    this.materialList.map((item, index) => {
                        item.new = a[index];
                    });
                });
                this.active = 0;
                console.log("this", this.materialList);

                // 过滤出掉接口的
                let p = res.filter((e) => {
                    return e.status == 1;
                });
                // console.log(p);
                // 判断上面过滤的数组是否里面都为空
                let p2 = p.every((e) => {
                    return e.data.length == 0;
                });
                // console.log(p2);
                if (!p2) {
                    this.pdfs = res.reduce((init, res) => {
                        let item = [];
                        if (res.status === "1") {
                            item = res.data.length ? res.data : [res.data];
                        } else if (res.notPdf) {
                            item = [res];
                        } else {
                            item = [];
                        }
                        return init.concat(item);
                    }, []);
                    // this.previewPdf(0, 1);
                    this.$loading.hide();
                } else {
                    this.display = "none";
                    this.checkHold(this.params);
                }
            });
        },
    },
    computed: {
        wordTip() {
            const word = this.pdfs[this.pdfIndex];
            const enrc = this.ENCR;
            const plans = this.productData.plans;
            console.log(enrc, "enrc");
            if (word && word.notPdf) {
                const planCodes = plans.map((v) => v.planCode.slice(0, -2));
                const risks = plans.map(
                    (v) =>
                        enrc.find((item) => item.risk === v.planCode.slice(0, -2)).riskName
                );
                const HI = enrc
                    .filter((v) => planCodes.includes(v.risk) && v.isHI)
                    .sort((a, b) => planCodes.indexOf(a.risk) - planCodes.indexOf(b.risk))
                    .map((v) => v.riskName);
                if (word.type === "exoneration") {
                    return `本产品计划由《${risks.join("》、《")}》组成，${word.materialName
                        }如下：`;
                } else {
                    console.log("HI", HI);
                    const HItip =
                        HI.length > 0 ? `。其中《${HI.join("》、《")}》为健康保险产品` : "";
                    return `本产品计划由《${risks.join("》、《")}》组成${HItip}，${word.materialName
                        }如下：`;
                }
            } else {
                return "";
            }
        },
        params() {
            console.log(this.info);
            let batchNo =
                this.productData.batchNo || this.batch.batchNo || this.batchNum || "";
            let channel = "";
            if (location.href.indexOf("channel") != -1) {
                channel = location.href.split("=")[1];
            } else {
                channel = this.channelCode;
            }
            let qwyChannel = sessionStorage.getItem("channelCode");
            if ("0037" === qwyChannel) {
                channel = qwyChannel;
            }
            console.log(channel);
            batchNo = batchNo.length > 3 ? batchNo : "";
            let card = this.batch.cardPwd ? { plchdCardPwd: this.batch.cardPwd } : {};

            let Time = JSON.parse(sessionStorage.getItem("planPage"));

            let insValiDate =
                this.productData.takeEffectFlag == 1
                    ? `${Time.time.slice(0, 4)}` +
                    `-${Time.time.slice(4, 6)}` +
                    `-${Time.time.slice(6, 8)}`
                    : `${Time.time.slice(0, 4)}` +
                    `-${Time.time.slice(4, 6)}` +
                    `-${Time.time.slice(6, 8)}` +
                    ` 24:00:00`; //保险生效期
            return {
                ...{
                    prdId: this.productData.prd_id, //产品id
                    orderCode: sessionStorage.getItem("SINGE_PAGE_orderfode"), //订单号
                    plchdCardNo:
                        JSON.parse(sessionStorage.getItem("insuranceInformation_PAGE"))
                            .plchdCardNo || "", //卡号
                    insPolcyIntndEfDt: JSON.parse(sessionStorage.getItem("planPage"))
                        .time, //保单生效日

                    plchdBrthDt:
                        this.info.choosedRela == "01"
                            ? util.getBirthday(this.SINGE_PAGE_from.idCard)
                            : util.getBirthday(this.MORE_PAGE_from.idCard), //投保人生日YYYYMMDD
                    plchdCrdtExpDt:
                        this.info.choosedRela == "01"
                            ? clearParams(
                                this.SINGE_PAGE_from.dateTime == "长期有效"
                                    ? 99991231
                                    : this.SINGE_PAGE_from.dateTime
                            )
                            : // : clearParams(this.MORE_PAGE_from.dateTime.replace(/-/g, "")), //证件失效时间YYYYMMDD
                            clearParams(
                                this.MORE_PAGE_from.dateTime == "长期有效"
                                    ? 99991231
                                    : this.MORE_PAGE_from.dateTime
                            ), //证件失效时间YYYYMMDD

                    plchdCrdtNo:
                        this.info.choosedRela == "01"
                            ? clearParams(this.SINGE_PAGE_from.idCard)
                            : clearParams(this.MORE_PAGE_from.idCard), //身份证号
                    plchdCrdtTpCd: "1010", //证件类型                                                   //A修改为1010
                    plchdEmailAdr:
                        this.info.choosedRela == "01"
                            ? clearParams(this.MORE_PAGE_from.email)
                            : clearParams(this.MORE_PAGE_from.email), //邮件
                    plchdGndCd:
                        this.info.choosedRela == "01"
                            ? util.getSex(this.SINGE_PAGE_from.idCard)
                            : util.getSex(this.MORE_PAGE_from.idCard), //性别
                    plchdMoveTelNo:
                        this.info.choosedRela == "01"
                            ? clearParams(this.SINGE_PAGE_from.phone)
                            : clearParams(this.MORE_PAGE_from.phone), //手机号
                    plchdNatCd: "156", //国籍                                                          //0156修改为156
                    plchdNm:
                        this.info.choosedRela == "01"
                            ? clearParams(this.SINGE_PAGE_from.userName)
                            : clearParams(this.MORE_PAGE_from.userName), //投保人姓名

                    plchdAndRcgnReTpCd: getRelationShip(
                        this.info.choosedRela,
                        util.getSex(this.MORE_PAGE_from.idCard || ""),
                        util.getSex(this.MORE_PAGE_from.idCardInsured || "")
                    ), //投被保人关系
                    rcgnBrthDt:
                        this.info.choosedRela == "01"
                            ? util.getBirthday(this.SINGE_PAGE_from.idCard)
                            : util.getBirthday(this.MORE_PAGE_from.idCardInsured), //被保人生日YYYYMMDD
                    rcgnCrdtExpDt:
                        this.info.choosedRela == "01"
                            ? clearParams(
                                this.SINGE_PAGE_from.dateTime == "长期有效"
                                    ? 99991231
                                    : this.SINGE_PAGE_from.dateTime
                            )
                            : clearParams(
                                this.MORE_PAGE_from.dateTimeInsured == "长期有效"
                                    ? 99991231
                                    : this.MORE_PAGE_from.dateTimeInsured
                            ), //被保人证件失效时间YYYYMMDD
                    rcgnCrdtNo:
                        this.info.choosedRela == "01"
                            ? clearParams(this.SINGE_PAGE_from.idCard)
                            : clearParams(this.MORE_PAGE_from.idCardInsured), //被保人身份证号
                    rcgnCrdtTpCd: "1010", //被保人证件类型
                    rcgnEmail:
                        this.info.choosedRela == "01"
                            ? clearParams(this.MORE_PAGE_from.email)
                            : clearParams(this.MORE_PAGE_from.email), //被保人邮件
                    rcgnGndCd:
                        this.info.choosedRela == "01"
                            ? util.getSex(this.SINGE_PAGE_from.idCard)
                            : util.getSex(this.MORE_PAGE_from.idCardInsured), //性别
                    rcgnNatCd: "156", //国籍
                    rcgnNm:
                        this.info.choosedRela == "01"
                            ? this.SINGE_PAGE_from.userName
                            : this.MORE_PAGE_from.userNameInsured, //被保人名
                    //todo:
                    // channelCode: channel == '' ? this.channelCode : '', //销售渠道
                    channelCode: channel == "" ? this.channelCode : channel, //销售渠道
                    plchdProvCd: this.productData.plchdProvinceCode, //省350000 210000.211300.211303
                    plchdCityCd: this.productData.plchdCityCode, //市350100
                    plchdCntyAndDstcCd: this.productData.plchdDistrictCode, //区350103
                    rsdntTpCd: this.productData.plchdCityFlag, //this.productData.rsdntTpCd, //城市/农村0
                    appNoNum: this.productData.plchdPrtCount, //需要投保单号的数量
                    totInsPremAmt: this.productData.initPyFAmt, //个人付费金额
                    coPyFAmt: this.productData.coPyFAmt, //企业付费金额
                    interfaceCode: this.productData.funcFlag, //业务编码
                    channel: channel,
                    batchNo, // 批次号
                    classId:
                        this.info.choosedRela == "01"
                            ? this.SINGE_PAGE_from.classId
                            : this.MORE_PAGE_from.classId, // 班级id

                    // 添加--传日期给后端判断
                    insurear: this.productData.plans[0].insuYear, //保险期间
                    insurearflag: this.productData.plans[0].insuYearFlag, //保险期间单位[Y-年、M-月、D-天、A-年龄]
                    insValiDate, //保险生效期
                },
                ...card,
            };
        },
        ...mapState({
            userCode: (state) => state.app.userCode,
            phone: (state) => state.app.phone,
            userInfo: (state) => state.app.policyInfo,
            hasUserInfo: (state) => state.app.hasUserInfo,
            hasSomeUserInfo: (state) => state.app.hasSomeUserInfo,
            batch: (state) => state.app.batch,
            channelCode: (state) => state.app.channelCode,
            minDate: (state) => state.app.minDate,
            maxDate: (state) => state.app.maxDate,
            batchInfo: (state) => state.app.batchInfo,
            importActiveFlag: (state) => state.app.importActiveFlag,
            checkPhoneID: (state) => state.app.checkPhoneID,
            hasCheckHold: (state) => state.app.hasCheckHold,
            batchNum: (state) => state.app.batchNo,
            routeQuery: (state) => state.app.routeQuery,
        }),
    },
};
</script>

<style lang='less' scoped>
/deep/ .van-tab--active {
